a {
  display: contents;
  text-decoration: none;
}

.ReactGridGallery_tile {
  background: rgba(255, 255, 255, 1) !important;
}

.ReactGridGallery_custom-overlay {
  opacity: 1 !important;
}

.ReactGridGallery_tile-icon-bar {
  opacity: 0 !important;
  z-index: 100;
}

.ReactGridGallery_tile-icon-bar svg {
  fill: rgba(0, 0, 0, 0);
}

.ReactGridGallery_tile-bottom-bar > div {
  margin-right: 0 !important;
  float: right;
}

div.ReactModal__Overlay--after-open,
div.ReactModal__Content--after-open {
  z-index: 10000;
}

#practice-container
  div[style*="width: 100%"][style*="height: 100%"][style*="position: absolute"] {
  background-color: antiquewhite;
  border-radius: 15px 0 0 15px;
}

#practice-container
  div[style*="background-size: cover"][style*="100%,"][style*="background-image:"][style*=".png"] {
  visibility: hidden;
}
